@import url(https://fonts.googleapis.com/css?family=Cutive+Mono);
html {
  font-family: 'Cutive Mono', monospace; }

nav {
  position: fixed;
  background-color: lightgray;
  right: 30px;
  top: 20px;
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right; }
  nav a {
    display: inline-block;
    margin: 15px 10px 15px 10px;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    color: gray;
    font-size: 1.3em;
    text-decoration: none; }
    nav a::after {
      content: "_"; }

section {
  margin: 40px 40px 40px 40px;
  padding: 10px; }
  section > h1::after {
    content: "_"; }

#skills .skillSection {
  display: inline-block;
  vertical-align: top;
  margin: 20px 80px 20px 0; }

.skill {
  padding-bottom: 10px;
  cursor: pointer; }

.skillName {
  display: inline-block;
  width: 100px;
  text-align: right;
  margin-right: 10px; }

.selectedSkill {
  background-color: yellow; }

.skillScore {
  font-size: 1.4em;
  vertical-align: middle; }
  .skillScore .skillDeficit {
    color: lightgray; }

#portfolio article {
  margin: 50px 80px 80px 0;
  display: inline-block; }

.date {
  color: gray; }
  .date::before {
    content: "// "; }

.preview {
  display: inline-block;
  position: relative;
  isolation: isolate;
  border: 1px solid rgba(128, 128, 128, 0.4);
  font-size: 0; }
  .preview img {
    width: 500px;
    height: 500px; }
  .preview .run {
    position: absolute;
    width: 500px;
    height: 500px;
    top: 0;
    left: 0;
    font-size: 128px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    text-align: center;
    line-height: 500px;
    cursor: pointer;
    background-color: white;
    mix-blend-mode: luminosity;
    opacity: 0; }
    .preview .run:hover {
      opacity: 0.5; }

.info {
  display: inline-block;
  padding-left: 20px; }

.source {
  margin: 10px 0 10px 0; }
  .source a {
    text-decoration: none;
    color: black; }
  .source .sprite {
    background-image: url(data:image/png;base64,);
    background-repeat: no-repeat;
    display: inline-block;
    vertical-align: middle;
    margin-right: 5px; }
  .source .sprite-CodePen {
    width: 32px;
    height: 32px;
    background-position: -5px -5px; }
  .source .sprite-GitHub {
    width: 32px;
    height: 32px;
    background-position: -47px -5px; }

.techs li::before {
  content: "#";
  color: gray; }

ul {
  list-style-type: none;
  margin: 0;
  padding: 0; }

#resume, #contact {
  display: inline-block;
  vertical-align: top; }

#resume article {
  margin: 40px 60px 50px 0; }
  #resume article h2 {
    text-transform: uppercase; }

#contact ul {
  margin-bottom: 20px; }
  #contact ul:nth-of-type(1) {
    background-color: lightgreen; }

